import { useState } from 'react';
import { Menu, Transition } from '@headlessui/react';
import { NavLink }   from 'react-router-dom';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faBell, faAlignJustify } from '@fortawesome/free-solid-svg-icons';
import { getNavigationMenu } from '../config/Config';

const MyListNavbar = () => {
    return (
        <div className="grid grid-cols-5 gap-0 justify-items-center ">
            {/* APP NAME  */}
             <div className="flex p-4 ">
              <img  src={require('../esdm.png')} className="w-7 h-7 flex-initial -mt-1" />
              <h2 className="text-white font-bold text-center pl-2">
                  SIKK KEBTKE
              </h2>
            </div>
            {
              // Rendering Menu Navbar
              getNavigationMenu().map((menu, index) => {
                return (
                  <div className="text-white pt-4 "  key={index}>
                    <NavLink to={menu.to} as="div" className={({isActive}) => `p-4 pb-3 pl-5 pr-5 ${menu.addClass}` + (isActive ? " border-b-4 bg-blue-700" : " ") }> 
                          { menu.icon }
                          <span className="ml-1 text-base ">
                              {menu.title}
                          </span>
                    </NavLink>
                  </div>
                )
              })
            }
        </div>
    )
}


const ProfileDropDown = () => {
  return (
    <Menu as="div" className="relative">
      <Menu.Button>
        <div className=" pt-2 ">
            <img src={require("../profile.jpg")} className="w-10 h-10 rounded-full bg-white"/>                
        </div>
      </Menu.Button>
      <Transition
        enter="transition duration-100 ease-out"
        enterFrom="transform scale-95 opacity-0"
        enterTo="transform scale-100 opacity-100"
        leave="transition duration-75 ease-out"
        leaveFrom="transform scale-100 opacity-100"
        leaveTo="transform scale-95 opacity-0"
      >

      
      <Menu.Items className="absolute shadow-lg radius-2 rounded-md text-black -ml-32 bg-white ">
        <Menu.Item >
          {({ active }) => (
            <a
              className={` rounded-md p-3 flex w-40 ${active && 'bg-blue-500 text-white'}`}
              href="/account-settings"
            >
              Profile Setting
            </a>
          )}
        </Menu.Item>
         <Menu.Item >
          {({ active }) => (
            <a
              className={` rounded-md p-3 flex ${active && 'bg-blue-500 text-white'}`}
              href="/account-settings"
            >
              Logout
            </a>
          )}
        </Menu.Item>
      </Menu.Items>
      </Transition>
    </Menu>
  )
}
const MobileMenu = () => {
  return (
    
       <Menu as="div" className="relative">
      <Menu.Button>
        <div className="pt-5 pb-4 pl-2 ml-2 text-white">
              <FontAwesomeIcon icon={faAlignJustify} />
          </div>
      </Menu.Button>
      <Transition
        enter="transition duration-100 ease-out"
        enterFrom="transform scale-95 opacity-0"
        enterTo="transform scale-100 opacity-100"
        leave="transition duration-75 ease-out"
        leaveFrom="transform scale-100 opacity-100"
        leaveTo="transform scale-95 opacity-0"
      >
        <Menu.Items className="absolute shadow-lg radius-2 rounded-md text-black bg-white w-64">

       {
          // Rendering Menu Navbar
          getNavigationMenu().map((menu, index) => {
            return (
                <Menu.Item key={index}>
                {({ active }) => (


                  <NavLink to={menu.to} as="div" className={({isActive}) => "rounded-md p-3 flex p-4 pb-3 pl-5 pr-5" + (isActive ? "  text-white  border-b-4 bg-blue-600" : " ") }> 
                          { menu.icon }
                          <span className="ml-1 text-base ">
                              {menu.title}
                          </span>
                    </NavLink>
               
                )}
              </Menu.Item>
            )
          })
        }
  
      </Menu.Items>
      </Transition>
    </Menu>
  )
}
export const Navbar = ({ children }) => {
    const [expand, setExpand] = useState(false);
    let isHide  = (!expand) ? "hidden" : "";
    return (
        <nav className="w-screen ">
            <div className="z-40 bg-blue-500 pl-4 shadow-lg">
                { /* Ketika navbar di mobile */ }
                <div className="flex md:hidden">
                    <MobileMenu />
                    <div className="p-5">
                       
                        <h2 className="text-white font-bold text-center">
                            SIKK KEBTKE
                        </h2>
                    </div>
                </div>
                <div className= "hidden md:justify-between md:flex">
                        
                        { /*  Ketika navbar di desktop */ }
                    <MyListNavbar/>

                    <div className="hidden grid grid-cols-3 text-white md:inline-grid">
                        <div className="pt-5">
                            <center>
                              <FontAwesomeIcon icon={faBell} className="text-lg" />
                            </center>

                        </div>
                 
                        <ProfileDropDown />
                    </div>
                </div>
            </div>
        </nav>
    )
}