import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faUser, faChartPie, faChartLine, faList, faBell } from '@fortawesome/free-solid-svg-icons'

export const getNavigationMenu = () => {
        return [
            // dashboard
            {
                title: "Dashboard",
                to: "/app/dashboard",
                icon: <FontAwesomeIcon  icon={faChartPie} className="mt-0.5"/>
            },
            // pelanggan
            {
                title: "Pelanggan",
                to: "/app/pelanggan",
                icon: <FontAwesomeIcon  icon={faUser} className="mt-0.5"/>,
                addClass: " -ml-1 "
            },
            // transaksi
            {
                title: "Transaksi",
                to: "/app/transaksi",
                icon: <FontAwesomeIcon  icon={faList} className="mt-0.5"/>,
                addClass: " -ml-8 "
            },
            // Laporan
            {
                title: "Laporan",
                to: "/app/laporan",
                icon: <FontAwesomeIcon  icon={faChartLine} className="mt-0.5"/>,
                addClass: " -ml-20 "
            }
        ]
    
}
