
import Tab from './Transaksi/Tab';
import { Card, InfoCard, ChipTable } from '../components/Card';
import { InputText, TextArea, Radio, Select, AutoComplete } from '../components/Input';

export default () => {
    return (
        <div className="p-5">
            <Tab />
            <div className="pt-3 grid grid-cols-3 gap-3">
                <Card>
                    <div className="p-3 pb-4 ">
                        <Select Title="Nama Layanan" Placeholder = "Nama Konsumen" Name="nama_konsumen" Status="">
                            <option value="">Jenis Layanan</option>
                            <option value="Pelatihan Teknis">Pelatihan teknis</option>
                            <option value="Pelatihan Non Teknis">Pelatihan Non Teknis</option>
                            <option value="Sertifikasi">Sertifikasi</option>
                            <option value="BIMTEK">BIMTEK</option>
                            <option value="Layanan Penunjang">Layanan Penunjang</option>
                        </Select>
                        <br />
                        <InputText Title="Nama Kegiatan" Placeholder = "Nama Kegiatan" Name="nama_kegiatan" Status="" />
                        <br />
                        <AutoComplete Title="Konsumen" Placeholder = "Cari Konsumen" Name="nama_konsumen" Status="" />
                    </div>
                    
                </Card>
                <div className="col-span-2">
                    <Card>
                        <h5>List Transaksi</h5>
                    </Card>
                    
                </div>
                
            </div>
         
        </div>
    )
}