export default () => {
    const active = "bg-blue-500 hover:text-blue-500 active:bg-gray-100"
    // noneactive => bg-gray-100
    return (
        <div className="grid grid-cols-4 gap-2 bg-gray-200 rounded-md p-2 justify-center">
            <div className="">
                <button className={active + " ease-in duration-100 rounded-md hover:bg-white hover:shadow-md text-white w-full p-2 shadow-md"}>Buat Transaksi</button>
            </div>
            <div>
                <button className=" bg-gray-100 active:bg-gray-100 ease-in duration-100 rounded-md hover:bg-white hover:shadow-md w-full  p-2">
                    Belum Diajukan
                </button>
            </div>
            <div>
                <button className="bg-gray-100 active:bg-gray-100 ease-in duration-100 rounded-md hover:bg-white hover:shadow-md w-full p-2">
                    Berjalan
                </button>
            </div>
            <div>
                <button className="bg-gray-100 active:bg-gray-100 ease-in duration-100 rounded-md hover:bg-white hover:shadow-md w-full p-2">
                    Selesai
                </button>
            </div>
        </div>
    )
}