import { useState, useEffect } from 'react';
import logo from './logo.svg';
import './App.css';
import { BrowserRouter, Navigate, Route, Routes, useNavigate  } from "react-router-dom";
import { Navbar } from './components/Navbar';
import Dashboard  from './menu/Dashboard';
import Pelanggan  from './menu/Pelanggan';
import Transaksi  from './menu/Transaksi';
import Laporan  from './menu/Laporan';
import Login from './menu/Login';
import Buat from './menu/Transaksi/Buat';

function App() {
  let navigate  = useNavigate();
  const [state, setstate] = useState({
    isLogin: true
  })
  
  const verifyLogin = () => {
    setstate(prevState => ({ ...prevState, isLogin: true }))
  }
  useEffect(() => {
      let myUrl = new URL(window.location.href)  
      let pathname = myUrl.pathname
      console.log(myUrl)
      let myBody = document.getElementById("root");
      myBody.classList.add("overflow-y-hidden")
      //    {isLogin ? <Navigate  to="/index.html/dashboard" /> : <Navigate  to="/index.html/login" />}
      if(state.isLogin && (pathname == "/index.html/" || pathname == "/" || pathname == "/index.html")){
        navigate(`/app/dashboard`);
      }
    return () => {
    }
  }, [])
  return (
     <div>
      {
        (state.isLogin) ? 
          <Navbar />
        
        :
        null
      }                   

          {
            (state.isLogin) ? 
              <Routes>
                <Route path="/app" element={<Dashboard />} />
                <Route path="/app/dashboard" element={<Dashboard />} />
                <Route path="/app/pelanggan" element={<Pelanggan />} />
                <Route path="/app/transaksi" element={<Transaksi />} />
         
               
                <Route path="/app/laporan" element={<Laporan />} />
              </Routes>

            : 
              <Routes>
                    <Route path="*" element={<Login verifyLogin={verifyLogin} />} />
              </Routes>
          }
        
         

         
     </div>
  );
}

export default App;
