
export const Card = ({ children }) => {
    return (
        <div>
            <div className="bg-white shadow-md rounded-md p-3 mb-5">
                {children}
            </div>
           
        </div>
    )
}
export const InfoCard = ({ param="bg-white", children }) => {
    return (
        <div className={" shadow-md rounded-md p-5 " + param}>
            {children}
        </div>
    )
}

export const ChipTable = ({icon, text}) => {
    return (
        <div className="rounded-xl bg-white shadow-sm mb-2">
            <div className="flex  justify-between">
                <div className="pl-2 pt-1 pb-1 flex-none">
                    { icon }
                </div>
                <div className="pl-2 pt-1 pb-1 flex-1 ">
                    { text }
                </div>
            </div>
        </div>
    )
}