import { useState } from 'react'
export const InputText = ({ Title, Type="text", Name, Placeholder, Status="" }) => {
    const State = (Status == "") ? "" : "border-red-500"
    return (
        <div>
            <div className="mb-1">{ Title }</div>
            <input type={Type} name={Name} className={"mb-1 border-2 p-2 pl-5 w-full rounded-md bg-gray-100 " + State} placeholder={Placeholder} />
            <div className="text-red-500 text-sm -mt-1 italic ">{ Status }</div>
        </div>
    )
}


export const TextArea = ({ Title, Type="text", Name, Placeholder, Status="" }) => {
    const State = (Status == "") ? "" : "border-red-500"
    return (
        <div>
            <div className="mb-1">{ Title }</div>
            <textarea type={Type} name={Name} className={"border-2 p-2 pl-5 w-full rounded-md bg-gray-100 " + State} placeholder={Placeholder} />
            <div className="text-red-500 text-sm -mt-1 italic ">{ Status }</div>
        </div>
    )
}

export const Radio = ({ Title, Name, Id, onChange, checked }) => {
    return (
        <div>
            <div className="form-check">
                <input checked={checked} onChange={onChange} className="form-check-input appearance-none rounded-full h-4 w-4 border border-gray-300 bg-white checked:bg-blue-600 checked:border-blue-600 focus:outline-none transition duration-200 mt-1 align-top bg-no-repeat bg-center bg-contain float-left mr-2 cursor-pointer" type="radio" name={Name} id={Id}/>
                <label className="form-check-label inline-block text-gray-800" htmlFor={Id}>
                    { Title }
                </label>
            </div>
        </div>
        
    )
}
export const Select = ({ Title, Name, Placeholder, Status="", Other={},children }) => {
    const State = (Status == "") ? "" : "border-red-500"

    return (
        <div>
            <div className="mb-1">{ Title }</div>
            <select {...Other} name={Name} className={"border-2 p-2 pl-5 w-full rounded-md bg-gray-100 " + State} placeholder={Placeholder}>
            {children}
            </select>
            <div className="text-red-500 text-sm -mt-1 italic ">{ Status }</div>
        </div>
    )
}

export const AutoComplete = ({ Title, Type="text", Name, Placeholder, Status="", Selected=false }) => {
    const State = (Status == "") ? "" : "border-red-500"
    const [Fokus, setFokus] = useState(false);
    return (
        <div>
            <div className="mb-1">{ Title }</div>
            <input type={Type} name={Name} onFocus={() => { setFokus(true) }} className={"mb-1 relative border-2 p-2 pl-5 w-full rounded-md bg-gray-100 " + State} placeholder={Placeholder} />
            {
                (Fokus) ?
                <div className="h-40 absolute bg-white pl-3 pt-2 shadow-lg pt-1 pr-3 rounded-b-lg overflow-y-scroll">
                    <ul>
                        <li className="bg-gray-100 hover:bg-blue-300 mb-1 p-1 pl-3 pr-3 rounded-md transition ease-in duration-100">PT Pertamina Cepu</li>
                        <li className="bg-gray-100 hover:bg-blue-300 mb-1 p-1 pl-3 pr-3  rounded-md transition ease-in duration-100">PPSDM MIGAS</li>
                        <li className="bg-gray-100 hover:bg-blue-300 mb-1 p-1 pl-3 pr-3  rounded-md transition ease-in duration-100">PEM AKA Migas</li>
                    </ul>
                </div>
                : 
                null
            }
            
            <div className="text-red-500 text-sm -mt-1 italic ">{ Status }</div>
        </div>
    )
}
